@use "base/variables" as v;
@use "base/mixins" as m;

.boton-red {
  background: rgb(174, 56, 131);
  background: linear-gradient(
    0deg,
    rgba(174, 56, 131, 1) 0%,
    rgba(200, 68, 107, 1) 100%
  );
  padding: 1.5rem 4rem;
  color: v.$blanco;
  margin: 2rem auto;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 90%;
  border-radius: 5rem;
  align-self: center;

  @include m.tablet {
    width: 35rem;
    max-width: 80%;
    margin: 1rem auto;
    font-size: 1.5rem;
  }
  &:hover {
    filter: brightness(80%);
    cursor: pointer;
    color: v.$blanco;
  }
}

.boton-verde {
  // background: rgb(109,170,70);
  // background: linear-gradient(0deg, rgba(109,170,70,1) 0%, rgba(140,183,67,1) 50%, rgba(180,198,60,1) 100%);
  @include m.bg-deg-verde;

  padding: 1.5rem 4rem;
  color: v.$blanco;
  margin: 2rem auto;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 90%;
  border-radius: 3rem;
