  }

  @include m.tablet {
    width: 35rem;
    max-width: 80%;
    margin: 1rem auto;
    font-size: 1.5rem;
  }
  &:hover {
    filter: brightness(80%);
    cursor: pointer;
    color: v.$blanco;
  }
}

.boton-azul {
  // background: v.$temaCatalogo;
  // background: linear-gradient(0deg, v.$temaCatalogo 0%, v.$temaSecundario 100%);
