.btn-secc{
  //@include m.bg-deg-tema;
  background-color: v.$temaCatalogo;
  font-size: 1.8rem;
  position: fixed;
  left: 0;
  right: 0;
  top: .5rem;
  z-index: 20;
  width: 10rem;
  border-radius: 1rem;
  margin: auto;
  border: none;

  color: v.$blanco;
  &:hover{
