@use 'variables' as v;

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}
@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($bgColor, $colorTexto) {
    background-color: $bgColor;
    padding: 1.5rem 4rem;
    color: $colorTexto;
    margin-top: 2rem;
    font-size: 2rem;
    font-weight: 400;
    display: inline-block;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    display: block;
    width: 100%;
    margin: 5rem 0;

    @include tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( $bgColor, 10% );
        cursor: pointer;
    }
}

@mixin grid($columnas, $espaciado ) {
    display: grid;
    gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin direcc{
    border-bottom-left-radius: 3rem;
    border-bottom-right-radius: 3rem;
    border: 1px solid v.$negro;
    width: 15rem;
    background-color: v.$blanco;
    text-align: center;
    position: fixed;
    bottom: 50%;
    z-index: 15;

  a{
      color: v.$negro;
      font-size: 2rem;
      margin: 1rem;
  }
}

@mixin bg-deg-verde{
    background: rgb(109,170,70);
    background: linear-gradient(0deg, rgba(109,170,70,1) 0%, rgba(140,183,67,1) 50%, rgba(180,198,60,1) 100%);
}

@mixin bg-deg-tema{
    background: v.$temaCatalogo;
    background: linear-gradient(0deg, v.$temaCatalogo 0%, v.$temaSecundario 100%);
}