@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5fadd4+0,60a9d2+29,61afd6+66,7db9e8+98 */
  /*   background: #5a96c6;
  background: linear-gradient(180deg,#5a96c6,#67a9e0 64%,#5caad3); */
  background-image: url("../img/header.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  overflow: hidden;
  position: relative;

  .bg-header {
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(255,255,255,0.17690826330532217) 35%);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .content-header {
    z-index: 10;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 65%;
    background-color: rgba(97,178,217,.7);
    margin-left: auto;
    margin-right: auto;
    border-radius: 5rem;
    padding: 2rem;
    width: 95%;
    
    @include m.tablet{
      width: 75%;
      bottom: 15rem;
    }
  }

  .boton-header{
    z-index: 10;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 5%;
    background-color: rgba(0, 0, 0, 0.6);
    margin-left: auto;
    margin-right: auto;
    border-radius: 5rem;
    padding: 2rem;
    width: 85%;
    
    @include m.tablet{
      width: 30%;
      bottom: 5rem;
    }
  }

  .title {
    margin: 3rem;
    text-transform: uppercase;
  }
  .subtitle {
    font-size: 20px;
    color: v.$blanco;
    text-align: center;

    span {
      font-weight: v.$bold;
    }
  }

  img {
    max-width: 70rem;
    margin-left: -10rem;
    @include m.tablet {
      position: absolute;
      right: 0;
      width: 80rem;
      max-width: 100%;
      margin-left: unset;
    }
  }
}
