/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #1a1b15;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  font-size: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.content {
  max-width: 85rem;
  margin: 2rem 1rem;
}
@media (min-width: 768px) {
  .content {
    width: 90%;
    margin: 2rem auto;
  }
}

@media (min-width: 768px) {
  .dos-tercios {
    width: 60%;
  }
}

@media (min-width: 768px) {
  .tercios {
    width: 30%;
  }
}

.text-center {
  text-align: center;
}

.flex {
  display: flex;
}

@media (min-width: 768px) {
  .medios {
    width: 40%;
  }
}

@media (min-width: 768px) {
  .flex-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.margin-neg-top-10 {
  margin-top: -5rem;
}
@media (min-width: 768px) {
  .margin-neg-top-10 {
    margin-top: -10rem;
  }
}

.img-height-10rem {
  height: 11rem;
}

.width-100 {
  width: 100%;
}

.img-width-10 {
  width: 10rem;
}

.cellphones {
  width: 25rem;
  display: block;
  margin: 0 auto;
}

.header {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5fadd4+0,60a9d2+29,61afd6+66,7db9e8+98 */
  /*   background: #5a96c6;
  background: linear-gradient(180deg,#5a96c6,#67a9e0 64%,#5caad3); */
  background-image: url("../img/header.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.header .bg-header {
  background: black;
  background: linear-gradient(0deg, black 0%, rgba(255, 255, 255, 0.1769082633) 35%);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.header .content-header {
  z-index: 10;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 65%;
  background-color: rgba(97, 178, 217, 0.7);
  margin-left: auto;
  margin-right: auto;
  border-radius: 5rem;
  padding: 2rem;
  width: 95%;
}
@media (min-width: 768px) {
  .header .content-header {
    width: 75%;
    bottom: 15rem;
  }
}
.header .boton-header {
  z-index: 10;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 5%;
  background-color: rgba(0, 0, 0, 0.6);
  margin-left: auto;
  margin-right: auto;
  border-radius: 5rem;
  padding: 2rem;
  width: 85%;
}
@media (min-width: 768px) {
  .header .boton-header {
    width: 30%;
    bottom: 5rem;
  }
}
.header .title {
  margin: 3rem;
  text-transform: uppercase;
}
.header .subtitle {
  font-size: 20px;
  color: #FFFFFF;
  text-align: center;
}
.header .subtitle span {
  font-weight: 700;
}
.header img {
  max-width: 70rem;
  margin-left: -10rem;
}
@media (min-width: 768px) {
  .header img {
    position: absolute;
    right: 0;
    width: 80rem;
    max-width: 100%;
    margin-left: unset;
  }
}

.section .title-container {
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#5368ab+0,5fb1d7+98 */
  background: #1d6fbb;
  /* Old browsers */
  background: -moz-linear-gradient(top, #1d6fbb 0%, #5fb1d7 98%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #1d6fbb 0%, #5fb1d7 98%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, #1d6fbb 0%, #5fb1d7 98%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="v.$temaCatalogo", endColorstr="v.$temaSecundario",GradientType=0 );
  /* IE6-9 */
  text-transform: uppercase;
  padding: 2rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #1a001a;
}
.section .title-container h3 {
  margin: 2rem 0 0 0;
}
.section .title-container p {
  color: #FFFFFF;
  font-weight: 700;
  margin: 0;
  text-align: center;
}

.row .title-card {
  text-align: center;
  font-weight: 700;
  font-size: 2rem;
}

/* Websites */
.card-beneficios {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem;
  border-radius: 4rem;
  border: 2px solid #1d6fbb;
}
.card-beneficios .beneficios {
  align-items: center;
}
.card-beneficios .beneficios picture {
  margin: 1.5rem;
}
.card-beneficios .beneficios .slag {
  color: #1d6fbb;
}
.card-beneficios .beneficios .description {
  color: #1a1b15;
}

.card-caracteristicas {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem;
  border-radius: 4rem;
  border: 2px solid #1d6fbb;
}
.card-caracteristicas .caracteristicas {
  display: flex;
  align-items: center;
  width: 100%;
}
@media (min-width: 768px) {
  .card-caracteristicas .caracteristicas {
    width: 50%;
  }
}
.card-caracteristicas .caracteristicas picture {
  margin: 2rem;
  width: 7rem;
}

.qr {
  margin: 2rem auto;
  border: 2px solid #1a1b15;
  border-radius: 4rem;
  align-items: center;
  justify-content: center;
  text-align: center;
}
@media (min-width: 768px) {
  .qr {
    width: 100%;
    height: auto;
  }
}

.etiqueta {
  margin: 2rem auto;
  border: 2px solid #1d6fbb;
  border-radius: 4rem;
  text-align: center;
}
.etiqueta h3 {
  color: #1a1b15;
  margin: 2rem;
  text-transform: uppercase;
}

.mant-mensual {
  text-align: left;
  margin: 0;
  font-size: 2rem;
}

/* Redes */
.row-red-social {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .row-red-social {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .row-red-social picture {
    position: relative;
    left: -4rem;
    height: fit-content;
    width: 13rem;
    background-color: #FFFFFF;
  }
}
.row-red-social img {
  width: 8rem;
}

.card-red {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding: 2rem;
  border-radius: 4rem;
  border: 2px solid #1d6fbb;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .card-red {
    flex-wrap: nowrap;
  }
}
.card-red picture {
  margin-bottom: 1rem;
}
.card-red h4 {
  margin: 0;
  font-size: 20px;
}
.card-red span {
  font-weight: 700;
}

.card-paquetes-container {
  position: relative;
  margin-bottom: 2rem;
}

.card-paquetes {
  background-color: #FFFFFF;
  background-clip: padding-box;
  /* !importanté */
  border: solid 2px transparent;
  /* !importanté */
  border-radius: 1em;
}
.card-paquetes:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin: -2px;
  /* !importanté */
  border-radius: inherit;
  /* !importanté */
  background: linear-gradient(to bottom, #1d6fbb, #FFFFFF);
}

.title-paquetes {
  display: flex;
  align-items: center;
  padding: 1rem;
}
@media (min-width: 768px) {
  .title-paquetes {
    margin-left: -6.5rem;
  }
}
.title-paquetes img {
  width: 5rem;
}
.title-paquetes h4 {
  font-size: 3rem;
  margin: 0 0 0 1rem;
}

@media (min-width: 768px) {
  .img-medio {
    max-width: 50rem;
    width: 50rem;
  }
}

/* QR */
@media (min-width: 768px) {
  .qr-card {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.card-qr {
  padding: 2rem;
  border-radius: 4rem;
  border: 2px solid #1d6fbb;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem;
}
.card-qr h4 {
  font-size: 3rem;
  color: #1d6fbb;
}

.card-code-qr {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 2rem;
}

.img-qr {
  margin-top: -5rem;
}
@media (min-width: 768px) {
  .img-qr {
    margin-top: -14rem;
  }
}

/* GMB */
@media (min-width: 768px) {
  .img-gmb {
    position: relative;
    left: -4rem;
    height: fit-content;
    width: 13rem;
    background-color: #FFFFFF;
  }
}

/* Display Queries */
.boton-red {
  background: #ae3883;
  background: linear-gradient(0deg, #ae3883 0%, #c8446b 100%);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin: 2rem auto;
  font-size: 2rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 90%;
  border-radius: 5rem;
  align-self: center;
}
@media (min-width: 768px) {
  .boton-red {
    width: 35rem;
    max-width: 80%;
    margin: 1rem auto;
    font-size: 1.5rem;
  }
}
.boton-red:hover {
  filter: brightness(80%);
  cursor: pointer;
  color: #FFFFFF;
}

.boton-verde {
  background: #6daa46;
  background: linear-gradient(0deg, #6daa46 0%, #8cb743 50%, #b4c63c 100%);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin: 2rem auto;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 90%;
  border-radius: 3rem;
  align-self: center;
  display: flex;
  align-items: center;
}
.boton-verde img {
  width: 10rem;
  margin-right: 1rem;
}
.boton-verde h2 {
  margin: 0;
  font-size: 2.5rem;
}
@media (min-width: 768px) {
  .boton-verde {
    width: 35rem;
    max-width: 80%;
    margin: 1rem auto;
    font-size: 1.5rem;
  }
}
.boton-verde:hover {
  filter: brightness(80%);
  cursor: pointer;
  color: #FFFFFF;
}

.boton-azul {
  background: #1d6fbb;
  background: linear-gradient(0deg, #1d6fbb 0%, #5fb1d7 100%);
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin: 2rem auto;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 90%;
  border-radius: 3rem;
  align-self: center;
  display: flex;
  align-items: center;
  border: none;
}
.boton-azul img {
  width: 10rem;
  margin-right: 1rem;
}
.boton-azul h2 {
  margin: 0;
  font-size: 2.5rem;
}
.boton-azul button {
  color: inherit;
}
@media (min-width: 768px) {
  .boton-azul {
    width: 35rem;
    max-width: 80%;
    margin: 1rem auto;
    font-size: 1.5rem;
  }
}
.boton-azul:hover {
  filter: brightness(80%);
  cursor: pointer;
  color: #FFFFFF;
}

.btn-secc {
  background-color: #1d6fbb;
  font-size: 1.8rem;
  position: fixed;
  left: 0;
  right: 0;
  top: 0.5rem;
