@use 'base/variables' as v;
@use 'base/mixins' as m;

/* Accordion */

.accordion{
    margin: 1rem;
    
}

.accordion-item{
    border: none;
}
.accordion-header{
    text-align: center;
}

.accordion-button {
    font-weight: v.$bold;
    font-size: 2rem;
    text-align: center;
    background: rgba(0, 0, 0, 0);

    &:hover{
        color: v.$blanco;
        background: rgba(0, 0, 0, 0);
    }
    &:after{
        background-image: none;
    }
}

.movil-display{
    @include m.tablet{
        display: none;
    }
}

.tablet-display{
    display: none;
    @include m.tablet{
        display: flex;
    }
}